import * as React from "react"
import "./wallpaper.css"
import { StaticImage } from "gatsby-plugin-image"

const Wallpaper = () => {
    return (
        <div className="wallpaperbox">
            <StaticImage class="wallpaper-light" src={"../images/Wallpaper-light.png"}/>
            <StaticImage class="wallpaper-dark" src="../images/Wallpaper-dark.png"/>
        </div>
    )
}


export default Wallpaper